import React, { useState, useEffect } from 'react';
import { SchedulerItem, SchedulerSlot } from '@progress/kendo-react-scheduler';
import '@progress/kendo-theme-default/dist/all.css';
import { useNavigate } from 'react-router-dom';
import DateService from '../../../core/date.service';
import SubmissionService from '../SubmissionService';
import styles from './ReactSchedule.module.css';
import './kendo-scheduler.css';
import SubmittedScheduleBannerComponent from './submitted-schedule-banner/SubmittedScheduleBannerComponent';
import AuthService from '../../../core/auth.service';
import useTableSize from '../../../shared/shared';
import DefaultTimeComponent from './activehours/DefaultTimeComponent';
import StandardTimeComponent from './activehours/StandardTimeComponent';
import moment from 'moment';
import leftIcon from '../../../assets/icons/chev-left.svg';
import rightIcon from '../../../assets/icons/chev-right.svg';
import generatedIcon from '../../../assets/icons/Generated-Duration.svg';
import originalIcon from '../../../assets/icons/Original-Duration.svg';
import pin12Icon from '../../../assets/icons/pin12.png';
import pin15Icon from '../../../assets/icons/pin15.png';
import sponsorshipIcon from '../../../assets/icons/sponsorship.png';
import freePlayIcon from '../../../assets/icons/freeplay.png';
import { padStart } from '@fullcalendar/react';
import { broadcastTimeIcon, standardTimeIcon } from '../../../assets';

function SubmissionSchedulerComponent() {
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(true);
  const [scheduleInfo, setSCheduleInfo] = useState({
    channelID: 0,
    channelName: '',
    period: '',
  });
  const [filterSchedule, setFilterSchedule] = useState({});
  const [scheduleID, setScheduleID] = useState(1);
  const [submissionID, setSubmissionID] = useState(1);
  const [disabledStatistics, setDisabledStatistics] = useState(true);
  const [disabledExport, setDisabledExport] = useState(true);
  const [isExporting, setIsExporting] = useState(false);
  const [scheduleScores, setScheduleScores] = useState('');
  const [tableWidth, tableHeight] = useTableSize();
  const clockStart = -6;
  const [isStandard, setIsStandard] = useState(true);
  const [history, setHistory] = useState([]);
  const [sampleData, setSampleData] = useState([]);
  const [currentYear, setCurrentYear] = useState(null);
  const [dateRange, setDateRange] = useState({
    start: new Date(),
    end: new Date(),
  });
  const [displayDate, setDisplayDate] = useState(
    new Date(Date.UTC(currentYear, 10, 1))
  );
  const [disablePrevious, setDisablePrevious] = useState(true);
  const [disableNext, setDisableNext] = useState(false);
  const [isGeneratedDuration, setIsGeneratedDuration] = useState(true);
  const [organisation, setOrganisation] = useState('');
  const isEmptyOrSpaces = (str) => {
    if (str === undefined) {
      return true;
    }
    return str === null || str.match(/^ *$/) !== null;
  };

  const parseAdjust = (eventDate) => {
    const date = new Date(eventDate);
    date.setFullYear(currentYear);
    return date;
  };

  const getGeneratedScheduleDetails = (data) => {
    SubmissionService.getCalendarDetails(
      data.scheduleID,
      data.channelID,
      1,
      1
    ).then((response) => {
      if (response == false) {
        navigate(`${process.env.REACT_APP_SUBFOLDER}/not-found`);
      } else {
        if (data.id > 0) {
          setIsStandard(response.data.standardTime);
          const responseData = response.data.scheduleDetails.map(
            (dataItem, index) => ({
              id: index,
              start: parseAdjust(
                DateService.addHours(dataItem.startAdjusted, clockStart)
              ),
              end: parseAdjust(
                DateService.addHours(dataItem.endAdjusted, clockStart)
              ),
              startDate: dataItem.start,
              endDate: dataItem.end,
              title: dataItem.title,
              description: 'description',
              isAllDay: false,
              extendedProps: dataItem.extendedProps,
              duration: dataItem.duration,
              durationCurrent: dataItem.duration,
            })
          );
          setSampleData(responseData);
          setScheduleScores(response.data.scoreLabel);
          setOrganisation(response.data.organisationName);
        }
        setSCheduleInfo({
          channelID: response.data.channelID,
          channelName: response.data.channelName,
          period: data.period,
        });
        setIsLoading(false);
        setDisabledExport(response.data.disableExport);
        setDisabledStatistics(response.data.disableStatistics);
      }
    });
  };

  const callbackSchedule = () => {
    navigate(`${process.env.REACT_APP_SUBFOLDER}/submission`);
  };

  const handleExport = () => {
    if (!isExporting) {
      const queryString = `?ScheduleId=${scheduleID}&ChannelId=${scheduleInfo.channelID}&Standard=${isStandard}`;
      setIsExporting(true);
      SubmissionService.exportSubmissions(queryString)
        .then((response) => {
          const blob = new Blob([response.data], {
            type:
              'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
          });
          let periodSplit = scheduleInfo.period.split('-');
          let startSplit = periodSplit[0].split('/');
          let endSplit = periodSplit[1].split('/');

          const startString = startSplit[0] + startSplit[1] + startSplit[2];
          const endString = endSplit[0] + endSplit[1] + endSplit[2];
          let exportDate = new Date();
          const exportedDateString =
            padStart(exportDate.getDate(), 2).toString() +
            padStart(exportDate.getMonth() + 1, 2).toString() +
            exportDate.getFullYear().toString();

          saveAs(
            blob,
            `${organisation}_op2mise_schedule_export_${scheduleInfo.channelName}_${startString}_${endString}_${exportedDateString}.xlsx`
          );
        })
        .finally(() => {
          setIsExporting(false);
        });
    }
  };

  const handleExportStatistics = () => {
    if (!isExporting) {
      const queryString = `?ScheduleId=${scheduleID}&ChannelId=${scheduleInfo.channelID}`;
      setIsExporting(true);
      SubmissionService.exportStatistics(queryString)
        .then((response) => {
          const blob = new Blob([response.data], {
            type:
              'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
          });
          saveAs(
            blob,
            `${organisation}_op2mise_generated_statistics_export_${
              scheduleInfo.channelName
            }_${scheduleInfo.month}_${
              scheduleInfo.year
            }_${DateService.toExportFormat(new Date())}.zip`
          );
        })
        .finally(() => {
          setIsExporting(false);
        });
    }
  };

  const sampleDiv = (props) => {
    const { title } = props.dataItem;
    const grade = title.grade;
    const certificate = title.certificate;
    const pin = title.pin == null ? '' : title.pin;
    let timeStamp = DateService.toTimeFormat(props.dataItem.startDate);
    if (parseInt(timeStamp.split(':')[0]) < 6 && !isStandard) {
      const hh = parseInt(timeStamp.split(':')[0]) + 24;
      timeStamp = hh + ':' + timeStamp.split(':')[1];
    }
    const genre = '';
    let gradeCertificateString = grade;

    if (isEmptyOrSpaces(gradeCertificateString)) {
      gradeCertificateString = certificate;
    } else if (!isEmptyOrSpaces(certificate)) {
      gradeCertificateString = `${gradeCertificateString}, ${certificate}`;
    }

    if (isEmptyOrSpaces(gradeCertificateString)) gradeCertificateString = pin;
    else if (!isEmptyOrSpaces(pin))
      gradeCertificateString = `${gradeCertificateString}, ${pin}`;

    const durationInt =
      (props.dataItem.end.getTime() - props.dataItem.start.getTime()) /
      1000 /
      60 /
      60;
    const durationSplit = props.dataItem.durationCurrent.split(':');
    const duration =
      parseInt(durationSplit[0]) * 60 + parseInt(durationSplit[1]);
    return (
      <div
        title={`${timeStamp} (${duration} mins) ${genre}\n${title.titleName}\n${grade}\n${certificate}`}
        className={styles.divParent}
      >
        <div className={styles.divChild}>
          <small className={styles.lblTimeStampCalendarNoWrap}>
            {timeStamp} ({duration} mins){genre}
          </small>
          <br />
          <b style={{ color: '#0c212b' }} className={styles.lblTitleCalendar}>
            {title.titleName}
          </b>
          <small className={styles.txtGradeCertificate}>
            {gradeCertificateString}
          </small>

          <div style={{ marginTop: '10px' }}>
            {pin.includes('PIN 15') && (
              <img src={pin15Icon} style={{ height: '20px' }} title="PIN 15" />
            )}
            {pin.includes('PIN 12') && (
              <img src={pin12Icon} style={{ height: '20px' }} title="PIN 12" />
            )}
            {title.freePlay && (
              <img
                src={freePlayIcon}
                style={{ height: '20px' }}
                title="Free play"
              />
            )}
            {title.sponsorship && (
              <img
                src={sponsorshipIcon}
                style={{ height: '22px' }}
                title="Sponsorship"
              />
            )}
          </div>
        </div>
      </div>
    );
  };

  const computeHeight = (props) => {
    const slots = props.slots;
    let labeledSlots = 0;
    slots.map(function(value) {
      if (value.start.getMinutes() === 0) labeledSlots += 1;
    });

    const computedLabeledHeight = labeledSlots * 12;
    const computedHeight = (slots.length - labeledSlots) * 7;
    const totalHeight = computedLabeledHeight + computedHeight + 1;
    if (totalHeight > 20) return totalHeight + 'px';
    else return '0px';
  };

  const CustomItem = (props) => (
    <div>
      <SchedulerItem
        {...props}
        children={sampleDiv(props)}
        resizeHint={false}
        style={{
          ...props.style,
          backgroundColor: '#fff',
          color: '#0c212b',
          maxHeight: computeHeight(props),
          display: computeHeight(props) == '0px' ? 'none' : 'block',
        }}
      />
    </div>
  );

  const CustomViewSlot = (props) => (
    <SchedulerSlot
      {...props}
      style={{
        minHeight: props.isAllDay ? undefined : 7,
        height: props.isAllDay ? undefined : 7,
        maxHeight: props.isAllDay ? undefined : 7,
      }}
    />
  );

  const handleNextWeek = () => {
    const nextDate = DateService.addHours(displayDate, 168);
    setDisplayDate(parseAdjust(nextDate));
  };

  const handlePreviousWeek = () => {
    const nextDate = DateService.addHours(displayDate, -168);
    setDisplayDate(parseAdjust(nextDate));
  };

  const compareDate = (startDate, endDate, dateNow) => {
    var range = moment(dateNow).isBetween(startDate, endDate);
    return range;
  };

  useEffect(() => {
    const scheduledata = AuthService.getSelectedSubmission();
    if (scheduledata === null) {
      callbackSchedule();
    } else {
      setSubmissionID(scheduledata.data.id);
      setScheduleID(scheduledata.data.scheduleID);
      const periodString = scheduledata.data.period;
      const startStr = periodString.split('-')[0];
      const endStr = periodString.split('-')[1];
      const scheduleDate = new Date(
        Date.UTC(
          parseInt(startStr.split('/')[2]),
          parseInt(startStr.split('/')[1]) - 1,
          parseInt(startStr.split('/')[0]),
          0,
          0,
          0
        )
      );
      setDisplayDate(scheduleDate);
      setFilterSchedule(scheduledata.filter);
      setCurrentYear(parseInt(startStr.split('/')[2]));
      const endDate = new Date(
        Date.UTC(
          parseInt(endStr.split('/')[2]),
          parseInt(endStr.split('/')[1]) - 1,
          parseInt(endStr.split('/')[0]),
          0,
          0,
          0
        )
      );
      setDateRange({
        start: new Date(DateService.addHours(scheduleDate, 0)),
        end: new Date(DateService.addHours(endDate, 0)),
      });
      const user = AuthService.getUserIdentity();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (currentYear !== null) {
      setIsLoading(true);
      const scheduledata = AuthService.getSelectedSubmission();
      getGeneratedScheduleDetails(scheduledata.data);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentYear]);

  useEffect(() => {
    const datas = sampleData.map((dataItem, index) => ({
      id: index,
      start: parseAdjust(DateService.addHours(dataItem.startDate, clockStart)),
      end: parseAdjust(DateService.addHours(dataItem.endDate, clockStart)),
      startDate: dataItem.startDate,
      endDate: dataItem.endDate,
      title: dataItem.title,
      description: 'description',
      isAllDay: false,
      extendedProps: dataItem.extendedProps,
    }));
    setSampleData(datas);
    if (history.length > 0) {
      const hisotricdatas = history.map((dataItem, index) => ({
        id: index,
        start: parseAdjust(
          DateService.addHours(dataItem.startDate, clockStart)
        ),
        end: parseAdjust(DateService.addHours(dataItem.endDate, clockStart)),
        startDate: dataItem.startDate,
        endDate: dataItem.endDate,
        title: dataItem.title,
        description: 'description',
        isAllDay: false,
        extendedProps: dataItem.extendedProps,
      }));
      setHistory(hisotricdatas);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [clockStart]);

  useEffect(() => {
    var prev = DateService.addHours(displayDate, -168);
    var next = DateService.addHours(displayDate, 168);
    setDisablePrevious(!(new Date(prev) >= dateRange.start));
    const allowNext =
      displayDate.getDay() >= dateRange.end.getDay() &&
      displayDate < dateRange.end;
    setDisableNext(!(new Date(next) <= dateRange.end) && !allowNext);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [displayDate]);

  const computeDateTime = (selectedData) => {
    const duration = isGeneratedDuration
      ? selectedData.duration
      : selectedData.title.duration;
    const durationSplit = duration.split(':');
    const durationMinutes =
      parseInt(durationSplit[0]) * 60 + parseInt(durationSplit[1]);
    const startTime = parseAdjust(
      DateService.addHours(selectedData.startDate, clockStart)
    );
    const endTime = parseAdjust(
      DateService.addMinutes(startTime, durationMinutes)
    );
    return { start: startTime, end: endTime };
  };

  useEffect(() => {
    const datas = sampleData.map((dataItem, index) => ({
      id: dataItem.id,
      start: computeDateTime(dataItem).start,
      end: computeDateTime(dataItem).end,
      startDate: dataItem.startDate,
      endDate: dataItem.endDate,
      title: dataItem.title,
      description: 'description',
      isAllDay: false,
      extendedProps: dataItem.extendedProps,
      duration: dataItem.duration,
      durationCurrent: isGeneratedDuration
        ? dataItem.duration
        : dataItem.title.duration,
    }));
    setSampleData(datas);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isGeneratedDuration]);

  const scheduleHeader = () => {
    return (
      <div className={`col-12`} style={{ display: 'inline-block' }}>
        <button
          className={styles.saveButton}
          style={{
            float: 'left',
            fontSize: '20px',
            fontWeight: 'bolder',
            padding: '10px 20px',
            borderRadius: '4px 0px 0px 4px',
          }}
          onClick={handlePreviousWeek}
          type="button"
          title="previous"
          disabled={disablePrevious}
        >
          <img src={leftIcon} style={{ height: '17px' }} />
        </button>
        <button
          className={styles.saveButton}
          style={{
            float: 'left',
            fontSize: '20px',
            fontWeight: 'bolder',
            padding: '10px 20px',
            borderRadius: '0px 4px 4px 0px',
          }}
          onClick={handleNextWeek}
          type="button"
          title="next"
          disabled={disableNext}
        >
          <img src={rightIcon} style={{ height: '17px' }} />
        </button>
        <img
          className={`${styles.floatRight} ${styles.headerIcons}`}
          height="29px"
          title={isStandard ? 'View in broadcast time' : 'View in standard time'}
          src={isStandard ? standardTimeIcon : broadcastTimeIcon}
          onClick={() => setIsStandard(!isStandard)}
        />
        <img
          style={{
            float: 'right',
            marginRight: '15px',
            height: '29px',
            cursor: 'pointer',
            marginTop: '5px',
          }}
          title={
            isGeneratedDuration
              ? 'View original titles duration'
              : 'View generated titles duration'
          }
          src={isGeneratedDuration ? generatedIcon : originalIcon}
          onClick={() => {
            if (isGeneratedDuration) setIsGeneratedDuration(false);
            else setIsGeneratedDuration(true);
          }}
        />
      </div>
    );
  };

  return (
    <>
      <SubmittedScheduleBannerComponent
        componentName="Submissions"
        scheduleDetails={scheduleInfo}
      />
      <hr style={{ background: '#c8c8c8' }} />
      {isLoading ? (
        <div
          style={{
            height: 'calc(100vh - 200px)',
            position: 'relative!important',
          }}
        >
          <p
            style={{
              textAlign: 'center',
              position: 'absolute',
              top: '45%',
              left: '55%',
            }}
          >
            <i
              className={`${styles.faCircleNotchSpin} fas fa-circle-notch fa-spin`}
            />
          </p>
        </div>
      ) : (
        <>
          {submissionID > 0 && (
            <div className={`col-12`}>
              <small
                className={`${styles.divScheduleScores} `}
                title={`ID ${submissionID} ${scheduleScores}`}
              >
                <b>
                  ID
                  {submissionID}
                </b>{' '}
                {scheduleScores}
              </small>
            </div>
          )}
          {isStandard ? (
            <DefaultTimeComponent
              tableHeight={tableHeight}
              sampleData={sampleData}
              displayDate={displayDate}
              CustomViewSlot={CustomViewSlot}
              scheduleHeader={scheduleHeader}
              CustomItem={CustomItem}
              dateRange={dateRange}
            />
          ) : (
            <StandardTimeComponent
              tableHeight={tableHeight}
              sampleData={sampleData}
              displayDate={displayDate}
              CustomViewSlot={CustomViewSlot}
              scheduleHeader={scheduleHeader}
              CustomItem={CustomItem}
              dateRange={dateRange}
            />
          )}

          <div style={{ marginTop: '15px', float: 'right' }}>
            <button
              className={styles.cancelButton}
              onClick={callbackSchedule}
              type="button"
            >
              Close
            </button>
            <button
              className={styles.saveButton}
              style={{ marginLeft: '10px' }}
              onClick={handleExportStatistics}
              disabled={disabledStatistics}
              type="button"
              hidden={true}
            >
              Statistics
            </button>
            <button
              className={styles.saveButton}
              style={{ marginLeft: '10px' }}
              onClick={handleExport}
              disabled={disabledExport || isExporting}
              type="button"
            >
              Export
            </button>
          </div>
        </>
      )}
    </>
  );
}

export default SubmissionSchedulerComponent;
